body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 765px) {
  [className='pancho']{
    max-width: 100%;
    
  }
}

#navdiv{
  padding-top: 0px;
  height: 45px;
}

.dropdown{
  position: relative;
  display: inline-block;
}

.dropdowncontent{
  display: none;
  position: absolute;
  min-width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
   z-index: 1;
}

.dropdowncontent a{
  display: block;
  color: black;
  text-decoration: none;
  padding-top: 5px;
  padding-left: 5px;
  background-color: grey;
  
}

.dropdown:hover .dropdowncontent{
  display: block
}

#fixed{
  padding-top: 10px;
  padding-bottom: 15px 

}
#first{
  float: left
}


a:link{
  color: black;
  text-decoration: none 
}

a:visited{
  color: black
}

#left{
  float: right
}

#strong{
  font-size: 18px;
  padding-top: 10px;
  padding-bottom: 15px;
  padding-left: 10px;
}

#small{
  font-size: 12px;
  padding-bottom: 35px
}

.ileri{
  position: relative;
  width: 100%
}

.ileri img {
  width: 100%;
  height: auto;
}

.ileri .btn {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 25px;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: white;
  color: black;
  font-size: 12px;
  padding-top: 2px;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  text-align: center
}

.ileri .btn:hover{
  background-color: black;
}


